.p-recipes .-box-subscribe {
  margin-top: 0;
}

.p-recipes .-box-subscribe .inner {
  background: none;
}

.p-recipes .c-badge__list {
  position: absolute;
  bottom: 20px;
  right: 20px;
  z-index: 6;
  display: flex;
  justify-content: space-between;
  transition: all .3s ease-in;
  opacity: 1;
}

.p-recipes .c-badge__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 50px;
  text-align: center;
}

.p-recipes .c-badge__item.-level > .icon {
  background: url(/assets/img/svg/icon-chef-hat.svg) no-repeat center center #FFC416;
  background-size: 20px;
}

.p-recipes .c-badge__item.-time {
  display: block;
}

.p-recipes .c-badge__item.-time > .icon {
  background: url(/assets/img/svg/icon-clock.svg) no-repeat center center #43AB3F;
  background-size: 20px;
}

.p-recipes .c-badge__item:first-of-type {
  margin-right: 5px;
}

.p-recipes .c-badge__item > .icon {
  width: 40px;
  height: 40px;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  position: relative;
}

.p-recipes .c-badge__item > .text {
  font-size: 1.2rem;
  line-height: 1.3;
  color: #fff;
  text-align: center;
  margin-top: 5px;
}

@media (max-width: 896px) {
  .p-recipes .c-button-cmn-01 .text {
    position: relative !important;
  }
}

.p-recipes .c-scroll-down-line {
  background: url(/assets/img/recipes/img_recipes_line.png) no-repeat;
  width: 270px;
  height: 263px;
  position: absolute;
  top: 88px;
  left: 35px;
  background-size: cover;
}

@media (max-width: 896px) {
  .p-recipes .c-scroll-down-line {
    display: none;
  }
}

@media (min-width: 897px) and (max-width: 1439px) {
  .p-recipes .c-scroll-down-line {
    width: 18.75vw;
    height: 18.26389vw;
    top: 6.11111vw;
    left: 2.43056vw;
  }
}

.c-box-recipes .c-heading-egg {
  display: flex;
  flex-direction: column;
}

.c-box-recipes .c-heading-egg .c-heading-egg__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-right: 0;
}

.c-box-recipes .c-heading-egg-content {
  margin-top: 10px;
}
